import React, { Component } from 'react';
import "./style/iPhone.scss";

import iPhone from './img/iPhone-small.png';

export default class IPhoneSelection extends Component {
    constructor(props) {
        super(props);

        this.state = {
          selectedModel: null,
          selectedRepair: null,
        };

        this.selectModel = this.selectModel.bind(this);
        this.selectRepair = this.selectRepair.bind(this);
    }

    selectModel(model) {
        this.setState({
            selectedModel: model,
            selectedRepair: null,
        })
    }

    selectRepair(repair) {
        this.setState({
            selectedRepair: repair
        })
    }

    render() {
        const modelNames = ["5", "5S", "5C", "SE", "6", "6+", "6S", "6S+", "7", "7+", "8", "8+"];
        const models = modelNames.map((name, i) =>
            <div className={"model" + ((this.state.selectedModel === i) ? " selected" : "")} key={i}
                 onClick={() => this.selectModel(i)}>
                {name}
            </div>
        );

        const repairTypeNames = ["Screen", "Battery", "Charging Port", "Camera", "Other", "Not sure"];
        const repairTypes = repairTypeNames.map((name, i) =>
            <div className={"repairType" + ((this.state.selectedRepair === i) ? " selected" : "")} key={i}
                 onClick={() => this.selectRepair(i)}>
                {name}
            </div>
        );

        let stage = "stage-1";
        if (this.state.selectedModel !== null) {
            stage = "stage-2"
        }
        if (this.state.selectedRepair !== null) {
            stage = "stage-3"
        }

        return (
          <div className={"iPhoneSelection " + stage}>
              <div className="Select ">
                  {models}
                  {repairTypes}
              </div>
              <div className="Info">
                  <img src={iPhone} alt="iPhone" />
                  <hr/>
                  <div className="status">
                      The iPhone 6 screen can be done in 6 minutes and the <span className="stock">part is in stock</span>
                  </div>
                  <div className="price">
                      &pound;45
                  </div>
              </div>
          </div>
        );
    }
}


